@import '/src/styles/color/colors';
@import '/src/styles/responsive/responsive';


.projects-container{
    padding-bottom: 100px;
    background: url('../../assets//image/Background-Project.png');
    background-color: $light-blue;
    background-size: cover;
}

.projects-title{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding-left: 50px;
    padding-right: 50px;
    padding-bottom: 60px;
    gap: 1rem;
}

.projects-title h1{
    color: $white;
    font-size: 64px;
}

.projects-content{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4rem;
}

.projects-row{
    display: flex;
    flex-direction: row;
    gap: 4rem;
}

.projects-row  img{
    margin: 0;
    transition: all 0.2s;
    transform: translateY(0);
}

.projects-row .card-image img:hover{
    transform: translateY(5px) ;
}

.tech{
    display: flex;
    flex-direction: row;
    gap: 1rem;
    margin-top: 0.7rem;
}

.tech .list-tech{
    padding: 1px 15px;
    border-radius: 10px;
    color: $black;
    border: $black 1px solid;
    transition: linear 0.2s;
}

.tech .list-tech:hover{
    background-color: $light-blue;
    padding: 1px 15px;
    border-radius: 10px;
    color: $white-main;
    border: $white-main 1px solid;
}

