@media screen and (max-width: $large) {
    .projects-title h1{
        font-size: 50px;
    }
    
    .projects-title img{
        width: 50px;
    }

    .projects-row .card-image img{
        width: 30px;
    }
    
}

@media screen and (max-width: $slarge) {
    .projects-title h1{
        font-size: 46px;
    }
    
    .projects-title img{
        width: 46px;
    }

    .projects-row .card-image img{
        width: 30px;
    }
}

@media screen and (max-width: $medium) {

    .projects-container{
        background: url('../../assets//image/Background-Project-Mobile.png');
        background-color: $light-blue;
        background-size: cover;
    }

    .projects-title h1{
        font-size: 42px;
    }
    
    .projects-title img{
        width: 42px;
    }

    .projects-row{
        display: flex;
        flex-direction: column;
    }

    .projects-row .card-image img{
        width: 30px;
    }
}
@media screen and (max-width: $smedium) {
    .projects-title h1{
      font-size: 36px;
    }
  
    .projects-title img{
      width: 36px;  
    }
  
  }
@media screen and (max-width: $bsmall) {

    .projects-title{
        padding-left: 30px;
        padding-right: 30px;
    }

    .projects-title h1{
        font-size: 26px;
      }
    
      .projects-title img{
        width: 26px;  
      }

    .projects-row{
        display: flex;
        flex-direction: column;
    }

    .projects-row .card-image img{
        width: 30px;
    }
}

@media screen and (max-width: $small) {

    .contact-container{
        height: 100vh;
    }

    .projects-title{
        padding-left: 30px;
        padding-right: 30px;
    }

    .projects-title h1{
        font-size: 22px;
    }
    
    .projects-title img{
        width: 22px;
    }

    .projects-row{
        display: flex;
        flex-direction: column;
    }

    .projects-row .card-image img{
        width: 30px;
    }
}